<template>
  <div>
    <v-editFood
      v-if="edit.showEdit"
      :food="edit.food"
      @notShowEdit="notShow"
    ></v-editFood>
    <router-view></router-view>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/admin' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>活动管理</el-breadcrumb-item>
      <el-breadcrumb-item>活动列表</el-breadcrumb-item>
      <el-breadcrumb-item>活动详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- gutter 间距 -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
        
            placeholder="请输入内容"
            v-model="query"
            class="input-with-select"
          >
            <el-button
              :disabled="disabled.select"
              slot="append"
              icon="el-icon-search"
              @click="selectFood"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <router-link v-if="disabled.add == false" to="/addFood" 
            ><el-button :disabled = "disabled.add" type="primary">添加</el-button></router-link
          >
        </el-col>
      </el-row>

      <!-- 用户列表区 -->
      <el-table
        class="table"
        border
        stripe
        :data="allFood"
        style="width: 100%;"
      >
        <el-table-column type="index"></el-table-column>
        <el-table-column prop="id" label="id"> </el-table-column>
        <el-table-column prop="foodid" label="食品ID"> </el-table-column>
        <el-table-column prop="foodname" label="食品名称"> </el-table-column>
        <el-table-column prop="price" label="价格"> </el-table-column>
        <el-table-column prop="shopname" label="商店名称"> </el-table-column>
          <el-table-column prop="img" label="图片名称"> </el-table-column>
          <el-table-column  label="标签名称">
          
             <template  slot-scope = "scope">
              
               <span v-for="item in scope.row.tagDetail" :key="item.id">
              <el-tag size="mini" v-if="item.level === 0">{{item.tagname}}</el-tag>
              <el-tag size="mini" type="success" v-else-if="item.level === 1">{{item.tagname}}</el-tag>
              <el-tag type="warning" size="mini" v-else-if="item.level === 2">{{item.tagname}}</el-tag>
              <el-tag type="danger" size="mini" v-else-if="item.level === 3">{{item.tagname}}</el-tag>
              <el-tag type="info" size="mini" v-else-if="item.level >= 4">{{item.tagname}}</el-tag>
               </span>
          </template>
      </el-table-column>
          <el-table-column label="状态">
          <template slot-scope="scope">
            <el-tooltip
              class="item"
              effect="dark"
              content="是否禁用"
              placement="top"
              :enterable="false"
            >
              <el-switch
                :disabled="disabled.forbidden"
                v-model="scope.row.forbid"
                @change="forbidden(scope.row)"
              ></el-switch>
            </el-tooltip>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="300">
          <template slot-scope="scope">
            <el-tooltip
              class="item"
              effect="dark"
              content="编辑"
              placement="top"
              :enterable="false"
            >
              <el-button
              :disabled="disabled.update"
                type="primary"
                icon="el-icon-edit"
                size="small"
                @click="editFood(scope.row)"
              >
              </el-button>
            </el-tooltip>
            <el-tooltip
              class="item"
              effect="dark"
              content="删除"
              placement="top"
              :enterable="false"
            >
              <el-button
              :disabled="disabled.delete"
                type="danger"
                icon="el-icon-delete"
                size="small"
                @click="deleteFood(scope.row)"
              >
              </el-button>
              
            </el-tooltip>
            <el-button
              :disabled="disabled.update"
                type="primary"
                icon="el-icon-edit"
                size="small"
                @click="updateImgData(scope.row)"
              >更换图片
              </el-button>
          </template>
        </el-table-column>
      </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
    <el-dialog
  title="提示"
  :visible.sync="updateImg"
  width="30%"
  :before-close="handleCloseUpdateImg">
  <span>更换图片</span>
     <el-upload
              class="upload-demo"
              ref="upload"
              accept="image/png,image/gif,image/jpg,image/jpeg"
              :limit="limit"
              action="http://127.0.0.1:8088/uploadImg"
              :on-preview="handlePreview"
              :on-remove="handleRemove"
              :auto-upload="false"
              :data="imgData"
              :before-upload = "checkImg"
              :on-success= "onSuccess"
            >
              <el-button slot="trigger" size="small" type="primary"
                >选取文件</el-button
              >
              <el-button
                style="margin-left: 10px;"
                size="small"
                type="success"
                @click="submitUpload"
                >上传到服务器</el-button
              >
              <div slot="tip" class="el-upload__tip">
                只能上传jpg/png文件，且不超过500kb
              </div>
            </el-upload>
  <span slot="footer" class="dialog-footer">
    <el-button @click="updateImg = false">关闭</el-button>
  </span>
</el-dialog>
  </div>
</template>
<script>
import EditFood from "../edit/EditFood";
export default {
  components: {
    "v-editFood": EditFood
  },

  data() {
    return {
      //修改图片
      updateImg:false,
      imgData:{},
      limit:1,
      //权限信息
      autoArr: "",
      //是否显示
      disabled: {
        add: true,
        delete: true,
        update: true,
        select: true,
        forbidden: true
      },
      edit: {
        showEdit: false,
        food: {}
      },

      queryInfo: {
        pageNum: 1,
        pageSize: 5
      },
      query: "",
      allFood: [],
      total: 0,
      as: true
    };
  },
  created() {
    this.selectAllFood();
    this.getAutoArr()
  },
  methods: {
    async selectAllFood() {
      const { data: res } = await this.$http.post(
        "/selectAllFood",
        this.queryInfo
      );
      console.log("123456123")
      console.log(res);
      this.allFood = res.list;
      this.total = res.num;
      // this.allFood.forEach(element => {
      //   if(element.tagList !==null || element.tagList !== '' ){
      //      let lastTag = []
      //       element.lastTag = lastTag
      //       element.tagList.forEach(item =>{
      //       lastTag.push(item[item.length-1])
      //      })
      //   }
       
      // });
      console.log(this.allFood);
      // this.showAllAdmin = false;
      // this.showAllUser = true;
      // this.showAllRecord = false;
    },
    async selectFood() {
      const { data: res } = await this.$http.get("/selectFood", {
        params: { query: this.query }
      });
      if (res != null) {
        this.allFood = [];
        // for (let index = 0; index < res.length; index++) {
        //    console.log("1111111111111111");
        //   this.allFood.push(res[index]);
        //   console.log(res[index]);
        //   console.log("1111111111111111");
        //   console.log(this.allFood);
        // }
        this.allFood.push(res);
        console.log("allFood");
        console.log(res);
        console.log(this.allFood);
      } else {
        this.$message.error("没有这个人");
      }
    },
    // 监听pageSize改变是触发
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pageSize = newSize;
      this.queryInfo.pageNum = 1;
      this.selectAllFood();
    },
    // 页码值改变触发
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage;
      console.log(newPage);
      this.selectAllFood();
    },
    editFood(item) {
      console.log(this.edit.food);
      this.edit.showEdit = false;
      this.edit.food = item;
      this.edit.showEdit = true;
      console.log( this.edit.showEdit)
      console.log(this.edit.food);
    },
    async deleteFood(item) {
      const confirmResult = await this.$confirm(
        "将永久删除该用户！是否继续？",
        "提示",
        {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }
      ).catch(err => err);
      if (confirmResult == "cancel") return this.$message.info("已取消删除");

      console.log(item);
      const { data: res } = await this.$http.delete(`/food/${item.id}`);
      if (res.status == 0) {
        this.selectAllFood();
        return this.$message.success("删除成功！！");
      }
      return this.$message.error("删除失败！！");
      console.log(res);
    },
    async forbidden(item) {
      const { data: res } = await this.$http.put("/updateFoodState", item);
      console.log(res);
    },
    notShow(bool) {
      console.log("123");
      this.edit.showEdit = bool;
    },
    //获取权限
    async getAutoArr() {
      this.reset()
      const { data: res } = await this.$http.get("/getAuthorityString", {
        params: { id: window.sessionStorage.getItem("id") }
      });
      this.autoArr = res;
      console.log(res);
      var strs = new Array(); //定义一数组
      strs = res.split(","); //字符分割
      for (var i = 0; i < strs.length; i++) {
        if (strs[i] == "501") {
          this.disabled.add = false;
        } else if (strs[i] == "502") {
          this.disabled.delete = false;
        } else if (strs[i] == "503") {
          this.disabled.update = false;
        } else if (strs[i] == "504") {
          this.disabled.select = false;
        } else if (strs[i] == "505") {
          this.disabled.forbidden = false;
        }
      }
    },
    reset(){
      this.disabled.add = true
       this.disabled.delete = true
        this.disabled.update = true
        this.disabled.select = true
        this.disabled.forbidden = true
       
    },
      //图片文件校验
    checkImg(file){

      const isJPG = file.type === 'image/jpeg';
      const isPNG = file.type === 'image/png';
      const isPG = (isJPG || isPNG)                                       //限制图片格式为jpg / png
      const isLt2M = file.size / 1024 / 1024 < 2;                         //限制图片大小
      const isSize = new Promise(function(resolve,reject) {
          let width = 600
          let height = 400
          let _URL = window.URL || window.webkitURL
          let img = new Image()
          img.onload = function() {
              let valid = img.width >= width && img.height >= height
              valid ? resolve() : reject();   //将promise 转为resolve(成功) 或 reject(失败)状态
          }
          img.src = _URL.createObjectURL(file)
      }).then(() => {  //成功时回调
          return file;
      },()=>{     //失败时回调
          this.$message.error('上传图片像素要大于600*400!');
          return promise.reject();
      })
      if (!isPG) {
          this.$message.error('上传头像图片只能是 JPG 或 PNG 格式!');
      }
      if (!isLt2M) {
          this.$message.error('上传头像图片大小不能超过 2MB!');
      }
        return isPG && isLt2M && isSize
    },
    onSuccess(v1,file){
        console.log(file.type)
        // this.ruleForm2.img =  Math.random() + file.name
        this.$refs.upload.clearFiles();
        this.updateImg = false
     

    },
    //查询图片名称是否存在
     submitUpload() {
        this.$refs.upload.submit();
      },
      //打开更换图片弹框
    updateImgData(val){
        this.updateImg = true
        this.imgData = val
    },
    //关闭更换图片弹框
    handleCloseUpdateImg(){
      this.updateImg = false
    },
    handlePreview(file) {
        console.log(file);
      },
       handleRemove(file, fileList) {
        console.log(file, fileList);
      },
  }
};
</script>
<style scoped></style>
