<template>
  <div>
    <!-- <v-editUser
      v-if="edit.showEdit"
      :user="edit.user"
      @notShowEdit="notShow"
    ></v-editUser>
    <router-view></router-view> -->
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/admin' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>活动管理</el-breadcrumb-item>
      <el-breadcrumb-item>活动列表</el-breadcrumb-item>
      <el-breadcrumb-item>活动详情</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card class="box-card">
      <!-- gutter 间距 -->
      <el-row :gutter="20">
        <el-col :span="7">
          <el-input
            placeholder="请输入内容"
            v-model="query"
            class="input-with-select"
          >
            <el-button
              :disabled="disabled.select"
              slot="append"
              icon="el-icon-search"
              @click="selectAllFoodRecord"
            ></el-button>
          </el-input>
        </el-col>
        <el-col :span="4">
          <router-link to="/addUser"
            ><el-button :disabled="disabled.add" type="primary"
              >添加</el-button
            ></router-link
          >
        </el-col>
      </el-row>

      <!-- 用户列表区 -->
       <el-table
                  class="table"
                  border
                  stripe
                  :data="allFoodRecord"
                  style="width: 100%;"
                >
                  <el-table-column type="index"></el-table-column>
                  <el-table-column prop="id" label="订单ID">
                  </el-table-column>
                  <el-table-column prop="subject" label="店铺名">
                  </el-table-column>
                  <el-table-column prop="total_amount" label="金额">
                  </el-table-column>
                  <el-table-column prop="product_code" label="商品ID">
                  </el-table-column>
                  <el-table-column prop="body" label="订单详情">
                  </el-table-column>
                  <el-table-column prop="username" label="下单用户">
                  </el-table-column>
                  <el-table-column prop="userid" label="用户ID">
                  </el-table-column>
                  <el-table-column prop="out_trade_no" label="商品ID订单号">
                  </el-table-column>
                  <el-table-column prop="allneedtime" label="制作花费时间(分钟)">
                  </el-table-column>
                  <el-table-column  label="是否处理">
                     <template slot-scope="scope">
                        <i class="el-icon-success" style="color:lightgreen" v-if="scope.row.dispose === true"></i>
                        <i class="el-icon-error" style="color:red" v-else></i>
                     </template>
                  </el-table-column>
                  <el-table-column label="下单日期">
                    <template slot-scope="scope">
                      {{ scope.row.date | dateFormat }}
                    </template>
                  </el-table-column>
                  <el-table-column label="功能">
                      <template slot-scope="scope">
                     <el-button v-if="scope.row.dispose == false" type="success" size="mini" @click="disposeFoodRecord(scope.row)">处理</el-button>
                    </template>
                    
                  </el-table-column>
                </el-table>
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total"
      >
      </el-pagination>
    </el-card>
  
  </div>
</template>
<script>
export default {
  data() {
    return {
      //查询关键字
      query: "",
      //分页信息
      queryInfo: {
        pageNum: 1,
        pageSize: 5
      },
      total: 0,
      //权限显示
      disabled: {
        add: true,
        delete: true,
        update: true,
        select: true,
        detail: true
      },
      allFoodRecord: [],
      dialogVisible: false,

    };
  },
  created() {
    // this.selectAllRecord();
    this.getAutoArr();
  },
  methods: {
    async selectAllFoodRecord() {
      const { data: res } = await this.$http.post(
        `/selectAllFoodRecord/${this.query}`,
        this.queryInfo
      );
      console.log(res);
      console.log("124567897946");
      console.log(res);

        for (let index = 0; index < res.length; index++) {
              res[index].indate = this.timestampToTime (res[index].indate)
          }
      this.allFoodRecord = res.list;
      this.total = res.num;
    },
  
 
    handleClose() {},
    // 监听pageSize改变是触发
    handleSizeChange(newSize) {
      console.log(newSize);
      this.queryInfo.pageSize = newSize;
      this.queryInfo.pageNum = 1;
      this.selectAllFoodRecord();
    },
    // 页码值改变触发
    handleCurrentChange(newPage) {
      this.queryInfo.pageNum = newPage;
      console.log(newPage);
      this.selectAllFoodRecord();
    },
    async getAutoArr() {
      const { data: res } = await this.$http.get("/getAuthorityString", {
        params: { id: window.sessionStorage.getItem("id") }
      });
      this.autoArr = res;
      console.log(res);
      var strs = new Array(); //定义一数组
      strs = res.split(","); //字符分割
      for (var i = 0; i < strs.length; i++) {
        if (strs[i] == "101") {
          this.disabled.select = false;
        } else if (strs[i] == "404") {
          this.disabled.update = false;
        } else if (strs[i] == "403") {
          this.disabled.add = false;
        } else if (strs[i] == "401") {
          this.disabled.delete = false;
        } else if (strs[i] == "405") {
          this.disabled.detail = false;
        }
      }
    },
    //处理订单
    disposeFoodRecord(value){
      this.$confirm('您确定顾客已经收到该订单对应的食品了吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(async () => {
          console.log(value)
          const { data: res } = await this.$http.post(`/disposeFoodRecord/${value.id}/${value.userid}`, {})
          console.log(res)
          if(res.status === 0){
              this.$message({
            type: 'success',
            message: '已经向顾客发送确认订单请求!'
          });
          }else{
            this.$message.error('修改有误！！！')
          }
          
        }).catch(() => {
          this.$message({
            type: 'info',
            message: '已取消'
          });          
        });

    }
  }
};
</script>
<style scoped></style>
